body{
  margin: 0;
  padding: 0;
  text-align: center;
  background:rgb(0, 0, 0);
  background-size: cover;
  background-position: center;
  font-family: Poppins;
}

.Contact-title{
  margin-top: 100px;
  color: rgb(255, 0, 0);
  text-transform: uppercase;
  transition: all 1s ease-in-out;
}
.Contact-title h1{
  font-size: 32px;
  line-height: 10px;
}
.Contact-title h2{
  font-size: 16px;
}
.Contact-form{
  margin-top: 50px;
  transition: all 2s ease-in-out;
}

.form-control{
  width: 600px;
  background: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid rgb(255, 0, 0);
  color: rgb(198, 10, 245);
  font-size: 18px;
  margin-bottom: 16px;
}

input{
  height: 45px;
}

.submit{
  background: #ff5722;
  border-color: transparent;
  color: #fff;
  font-size: 20px;
  font-weight: 800;
  letter-spacing: 2px;
  height: 50px;
  margin-top: 20px;
}

form .submit:hover{
  background-color: #f44336;
  cursor: pointer;
}